.About {
    padding: 20px;
    background-color: #C663A4;
    display: flex;
    justify-content: space-between;
    padding-top: 32px;
    padding-bottom: 40px;
  }

.About {
    opacity: 1;
    transition: opacity 0.5s ease; 
  }
  
.About.visible {
    opacity: 0; 
  }
  
  
h1 {
    margin-bottom: 10px;
    font-size: 85px;
  }

p {
  font-size: 30px;
}

  
.button-container > *{
  background-color: #FDC435;
  margin: 10px;
  font-size: 18px;
  padding: 10px;
  }



.Left {
  height: 90vh; 
  width: 60vh;
  align-content: center;
  margin-left: 60px;
}

.Right {
  margin-right: 40%;
}

.circle1{
  position: fixed;
  height: 750px;
  width: 650px;
  background-color: #e8d5a5;
  border-radius: 49% 51% 48% 52% / 70% 42% 58% 30%;
  animation: animateCircle1 2s infinite alternate;

}

.circle2{
  position: fixed;
  height: 700px;
  width: 600px;
  margin-left: 20px;
  margin-top: 10px;
  background-color: #FDC435;
  border-radius: 35% 65% 24% 76% / 75% 35% 65% 25%; 
  animation: animateCircle2 2s infinite alternate;

}
@keyframes animateCircle1 {
  0% {
    border-radius: 49% 51% 48% 52% / 70% 42% 58% 30%;
  }
  100% {
    border-radius: 45% 55% 40% 60% / 60% 40% 60% 40%;
  }
}

@keyframes animateCircle2 {
  0% {
    border-radius: 35% 65% 24% 76% / 75% 35% 65% 25%;
  }
  100% {
    border-radius: 40% 60% 30% 70% / 70% 30% 70% 30%;
  }
}

img{
  height: 80vh;
  position: fixed;
  align-self: center;
  padding: 20px;
  margin-left: 40px;
  margin-top: 90px;
}