  /* Skills.css */

body {
  background-color: #C663A4; /* Cornflower Blue */
}

/* Import Slick Carousel CSS */
@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

/* Custom styles for the Skill component */
.skills-container {
  width: 90%;
  margin: auto;
  margin-top: 5rem;
}

.skill-card {
  background-color: pink;
  height: 450px;
  font: black;
  border-radius: 1rem;
  /* display: flex; */
  
  /* flex-direction: column; */
  /* justify-content: space-between; */
}

.skill-image-container {
  height: 56%;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;

  background-color: #6366F1; /* Indigo-500 */
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.skill-image {
  height: 11rem;
  width: 11rem;
  border-radius: 50%;
  margin-left: 1rem;
  margin-bottom: 5rem;
}

.skill-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
}

.skill-title {
  font-size: 1.25rem; 
  font-weight: 600; 
}

.slick-slide > div {
  margin: 0 10px;
}